@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

body {
  height: 100vh;
  width: 100vw;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  text-align: center;
}

h1 {
  font-size: 30px;
  font-weight: 200;
  background: #2edf72;
  color: white;
  padding: 10px 30px;
  border-radius: 5px;
  box-shadow: 1px 2px 5px #4b4849c5;
}

p {
  padding-top: 20px;
  font-size: 20px;
}
span {
  padding-top: 20px;
  font-size: 20px;
}

button {
  padding: 5px 10px;
  font-size: 18px;
  text-transform: uppercase;
  margin: 5px 10px;
  border-radius: 5px;
  border: 1px solid #e74a1a;
  color: #090000;
  background: #e58226;
  outline: none;
}

.context {
  font-size: 20px;
  padding-top: 10px;
}
